import Vue from 'vue'
import Router from 'vue-router'
// 引入组件方式一
import Part1 from '@/components/Part1'
import Part2 from '@/components/Part2'
import Part3 from '@/components/Part3'
// 引入组件方式二
const Part4 = () => import('@/components/Part4')
const Part5 = () => import('@/components/Part5')
// 引入组件方式三 (.default,原因:webpack打包时,如果是import的AMD规范,就是export default{//todo}),
// 若是require的CMD规范引入组件,就直接export了,要引入的在default中
const Part6 = require('@/components/Part6').default
const Part7 = require('@/components/Part7').default
Vue.use(Router)

const router1 = new Router({
  routes: [
    {
      path: '/',
      // redirect: '/Part1',
      // name: 'HelloWorld',
      // name: 'home',
      components: {
        // key和val一样的话,可以简写part1: part1 -->part1
        Part1,
        Part2,
        Part3,
        Part4,
        Part5: Part5,
        Part6: Part6,
        Part7: Part7
        // component: Home
      }
    },
    {
      path: '/Part1',
      name: 'Part1',
      component: Part1
    },
    {
      path: '/Part2',
      name: 'Part2',
      component: Part2
    },
    {
      path: '/Part3',
      name: 'Part3',
      component: Part3
    },
    {
      path: '/Part4',
      name: 'Part4',
      component: Part4
    },
    {
      path: '/Part5',
      name: 'Part5',
      component: Part5
    },
    {
      path: '/Part6',
      name: 'Part6',
      component: Part6
    },
    {
      path: '/Part7',
      name: 'Part7',
      component: Part7
    }
  ]
})
export default router1
// console.log(router1.options.routes[3].name)



