<template>
  <div :class='partSix'>
    <h3 class='text-center'>编程技能</h3>
    <p class='font-small'><span class='font-bold'>前端：</span>HTML/HTML5、CSS/CSS3、JS/ES6、jQuery、Vue、React</p>
    <p class='font-small'><span class='font-bold'>后端：</span>Node、JAVA</p>
    <p class='font-small'><span class='font-bold'>其他：</span>MongoDB、MySQL、Sqlserver</p>
  </div>
</template>

<script>
export default {
  name: 'partSix',
  data () {
    return {
      partSix: 'part-six',
      curWidth: 0
    }
  },
  beforeMount () {
    this.curWidth = document.documentElement.clientWidth || document.body.clientWidth
    if (this.curWidth < 1000) {
      this.partSix = 'part-six-responsive'
    }
  }
}
</script>

<style scoped>
.part-six {
  width: 100%;
  height: 310px;
  border: 15px solid transparent;
  border-image: url("~@/./assets/img/img6.png") 30 30 stretch;
  background: #18202d;
}
.part-six-responsive {
  width: 100%;
  border: 15px solid transparent;
  border-image: url("~@/./assets/img/img6.png") 30 30 stretch;
  background: #18202d;
}
.text-center {
  text-align: center;
}
.font-small {
  /* font-size: 1em; */
  font-size: 19px;
  line-height: 40px;
}
.font-bold {
  font-weight: bold;
  color: deepskyblue;
}
</style>



