<template>
  <div :class='partSeven'>
    <h3 class='text-center'>求职意向</h3>
    <p class='text-small'><span class='font-bold'>期望职位：</span>前端工程师</p>
    <p class='text-small'><span class='font-bold'>工作技能：</span>Vue</p>
    <p class='text-small'><span class='font-bold'>目标城市：</span>杭州、上海</p>
    <p class='text-small'><span class='font-bold'>期望薪资：</span>13K - 18K</p>
    <p class='text-small'><span class='font-bold'>入职时间：</span>随时入职</p>
  </div>
</template>

<script>
export default {
  name: 'partSeven',
  data () {
    return {
      partSeven: 'part-seven',
      curWidth: 0
    }
  },
  beforeMount () {
    this.curWidth = document.documentElement.clientWidth || document.body.clientWidth
    if (this.curWidth < 1000) {
      this.partSeven = 'part-sevev-responsive'
    }
  }
}
</script>

<style scoped>
.part-seven {
  width: 100%;
  height: 310px;
  border: 15px solid transparent;
  border-image: url("~@/./assets/img/img7.png") 30 30 stretch;
  background: #18202d;
}
.part-sevev-responsive {
  width: 100%;
  border: 15px solid transparent;
  border-image: url("~@/./assets/img/img7.png") 30 30 stretch;
  background: #18202d;
}
.text-center {
  text-align: center;
}
.text-small {
  line-height: 40px;
  /* font-size: .9em; */
  font-size: 17px;
}
.font-bold {
  text-align: center;
  color: deepskyblue;
}
</style>



